body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header {
  padding: 1rem 0;
}
.flex-child-element {
  padding: 0 10vw 0 10vw ;
  flex:1;
  align-self: center;
  margin: 5px;
}
.flex-parent-element {
  display: flex;
  overflow-wrap: anywhere ;
  flex-direction: column;
  color: #fffffe  ;
  background-color:  	#565294  ;
  min-width: 0;
  width: 70vw;
  max-width: 70vw;
  min-width: 500px;
  border: 2px solid #372554;
  border-radius: 1px;
  padding: 1rem 0;
  margin: 1dvb 0;
}
.Navigationbar{
  color: #fffffe;
  overflow-wrap: anywhere ;
  display: flex;
  flex-direction: row;
  min-width: 0;
  width: 100vw;
  max-width: 100vw;
  min-width: 40vw;
  max-height: 20vh;
}

.Navigationbarchild {
  text-decoration: none;
  font-size: 30px;
  flex:1;
  align-self: center;
  margin: 5px;
}
.Navigationtitle {
  background: transparent;
  text-decoration: none;
  font-size: 50px;
  flex:1;
  align-self: center;
  margin: 5px;
}
.ProductList{
  display: flex;
  flex-direction: row;
}
.ProductTitle{
  color:#b8c1ec;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 50px;
}

@media (max-width: 960px) {
  .ProductTitle{
    font-size: 30px;
  }
  
  .Navigationbarchild {
    font-size: 20px;
  }
  .Navigationtitle {
    font-size: 25px;
  }
  img.flex-child-element{
    width: 40vw;
  }
  .flex-parent-element {
    width: 70vw;
    max-width: 70vw;
    min-width: 30vw;
  }
}
